.aboutSection {
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  background-color: var(--color-242426);
  display: grid;
  grid-template-columns: 4fr 4fr;
  position: fixed;
}

.aboutSectionGradient {
  background-image: linear-gradient(
    to bottom right,
    var(--color-6e6b74),
    var(--admin-profile)
  );
}

.aboutSectionContainer {
  position: absolute;
  left: 50%;
  top: 50%;
  background-color: var(--color-242426);
  width: 80vw;
  height: 80vh;
  transform: translateX(-50%) translateY(-50%);
  box-shadow: -10px 10px 10px rgba(0, 0, 0, 0.192);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.aboutSectionContainer > h1 {
  font-size: 3rem;
  font-weight: 400;
  color: var(--color-white) !important;
  margin: 2vmax;
  text-shadow: 0 0 2px var(--color-black);
}

.aboutSectionContainer > div {
  display: flex;
  width: 100%;
}

.aboutSectionContainer > div > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
}

.aboutSectionContainer > div > div > p {
  color: var(--color-ff3e04) !important;
  font-size: 1rem;
  font-weight: 400;
  opacity: 0.8;
}

.aboutSectionContainer > div > div > button {
  margin: 1vmax 0;
}

.aboutSectionContainer > div > div > span {
  font-size: 1vmax;
  font-weight: 100;
  color: var(--color-white) !important;
  line-height: 1.6;
  text-align: center;
  width: 80%;
}

.aboutSectionContainer2 {
  border-left: 1px solid rgba(0, 0, 0, 0.116);
}

.aboutSectionContainer2 > h2 {
  color: var(--color-white) !important;
  font-size: 2rem;
  font-weight: 400;
  margin: 2vmax;
}

.aboutSectionContainer2 > a > svg {
  font-size: 4vmax;
}

.youtubeSvgIcon {
  color: red !important;
}

.instagramSvgIcon {
  color: rgb(144, 81, 202) !important;
}

@media screen and (max-width: 600px) {
  .aboutSectionContainer > div {
    display: block;
  }

  .aboutSectionContainer > h1 {
    font-size: 5rem;
    font-weight: 500;
    margin: 3vmax;
  }

  .aboutSectionContainer > div > div > p {
    font-style: 2vma;
    font-weight: 100;
  }

  .aboutSectionContainer > div > div > span {
    font-weight: 100;
    font-size: 1.2vmax;
    width: 70%;
  }

  .aboutSectionContainer2 {
    border-left: none;
  }

  .aboutSectionContainer2 > h2 {
    margin: 2vmax;
    font-size: 3vmax;
    font-weight: 100;
  }

  .aboutSectionContainer2 > a > svg {
    font-size: 6vmax;
    margin: 1vmax;
  }
}
